import React from 'react';
import classNames from 'classnames';
import { SectionSplitProps } from '../../utils/SectionProps';
import Image from '../elements/Image';
import Button from '../elements/Button';

const propTypes = {
  ...SectionSplitProps.types
}

const defaultProps = {
  ...SectionSplitProps.defaults
}

const FeaturesSplit = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {

  const outerClasses = classNames(
    'features-split section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-split-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const splitClasses = classNames(
    'split-wrap',
    invertMobile && 'invert-mobile',
    invertDesktop && 'invert-desktop',
    alignTop && 'align-top'
  );


  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <div className={splitClasses}>

            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-bottom" data-reveal-container=".split-item">
                <h1 className="mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200">
                    <span className="text-color-primary">CONSTRUCTION</span>
                  </h1>

                  <h2 className="mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200">
                      Building strong communities
                    </h2>

                <div className="container-xs">
                  <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
                    Our design team together with our construction department are devoted to create beautiful class-leading modern homes for your lifestyle.
                    </p>
                  <div className="reveal-from-bottom" data-reveal-delay="200">
                      <Button tag="a" color="primary" wideMobile href="#0">
                        Sign up
                        </Button>
                  </div>
                </div>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-right',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  className="has-shadow"
                  src={require('./../../assets/images/bulldozer.svg')}
                  alt="bulldozer"
                  width={500}
                  height={500} />
              </div>
            </div>

          </div>
        </div>
      </div>
    </section>
  );
}

FeaturesSplit.propTypes = propTypes;
FeaturesSplit.defaultProps = defaultProps;

export default FeaturesSplit;


